<template>
  <div class="jobFairBox">
    <div class="container">
      <label class="search-box">
          <input type="text" placeholder="快速搜索岗位、公司">
          <button>搜索</button>
      </label>
      <div class="quicklySearch">
        <ul>
          <li @click="changeType(index)" v-for="(item,index) in searchList" :key="index" :class="searchActive===index?'active':''">{{item.name}}</li>
        </ul>
      </div>
      <hr>
      <div>
        <el-menu  class="el-menu-demo" mode="horizontal" menu-trigger="click" :unique-opened="true">
          <el-submenu v-for="(item,index) in sortList" :key="index" :index="index+''">
            <template slot="title">{{getData(item)}}</template>
            <el-menu-item v-for="(arr,i) in item.list" :key="index+'-'+i" @click='changeSort(arr,item.type)'>{{arr.name}}</el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="jobContainer">
      <div class="container">
        <bread :data="[{name:'首页',url:'/'},{name:'职位招聘',url:''},{name:'专业不限'.name,url:''}]"></bread>
        <jobList :list="list" :total="total"></jobList>
        <div class="loginModule">
              <p class="title">环保行业职位任你选</p>
              <div class="formItem">
                <span>icon</span>
                <span>+86</span>
                <input type="text" class="phone" placeholder="请输入手机号码">
              </div>
              <div  class="formItem">
                <span>icon</span>
                <input type="password" class="psd" placeholder="请输入手机账号密码">
              </div>
              <div class="toLogin"><button type="button">登陆/注册</button></div>
              <div class="agree">
                <input type="checkbox">
                我已统一用户协议及隐私政策
              </div>
        </div>
        <otherInfo :list="list"></otherInfo>
      </div>
    </div>
  </div>
</template>
<script>
import api from '../api'
import jobList from './compoments/jobList'
import otherInfo from './compoments/otherInfo'
import bread from './compoments/bread'
export default {
  name:'JobFair',
  components:{
    jobList,
    otherInfo,
    bread
  },
  data(){
    return{
      eduReq:'0',
      expReq:'0',
      salaryReq:'0',
      comReq:'0',
      list:[],
      total:0,
      searchList:[{name:'专业不限'},{name:'环境工程'},{name:'环境污染与治理'},{name:'环境卫生工程'},{name:'环境化工'},{name:'环境监测'},{name:'环境科学'},{name:'生态学'},{name:'环境保护与监测'},{name:'环境规划与管理'},{name:'环境工程管理'},{name:'规划与环境保护'},{name:'环境监察'},{name:'环境治理工程'}],
      sortList:[
        {name:'工作经验',
        type:'exp',
        list:[
            {value:'0',name:'不限'},
            {value:'1',name:'1-3年'},
            {value:'2',name:'3-5年'},
            {value:'4',name:'5-10年'},
            {value:'5',name:'10年以上'}
          ]
        },
        {name:'学历要求',
        type:'edu',
        list:[
            {value:'0',name:'不限'},
            {value:'1',name:'中专及以上'},
            {value:'2',name:'大专及以上'},
            {value:'3',name:'本科及以上'},
            {value:'4',name:'硕士及以上'},
            {value:'5',name:'博士及以上'}
          ]
        },
        {name:'薪资要求',
        type:'salary',
        list:[
            {value:'0',name:'不限'},
            {value:'1',name:'5k以下'},
            {value:'2',name:'5-8k'},
            {value:'3',name:'8-12k'},
            {value:'4',name:'12-18k'},
            {value:'5',name:'18-25k'},
            {value:'6',name:'25k以上'}
          ]
        },
        {name:'公司规模',
        type:'com',
        list:[
            {value:'0',name:'20以下'},
            {value:'1',name:'20-150人'},
            {value:'2',name:'150-500'},
            {value:'3',name:'500-999'},
            {value:'4',name:'1000以上'}
          ]
        }
      ],
      searchActive:0
    }
  },
  methods: {
    init(){
      this.getList()
    },
    getList(){
      api.post('http://47.112.170.125:8080/env/position/getList',
      {eduReq:this.eduReq,expReq:this.expReq,salaryReq:this.salaryReq,positionName:'',postName:'',recruitType:0,comNumPeople:this.comReq}
      ).then(res=>{
        this.list = res.rows;
        this.total = res.total; 
      })
    },
    changeType(index){
      this.searchActive = index
    },
    changeSort(item,type){
      this[type+'Req'] = item.value;
      this.getList()
    },
    getData(item){
     let data = item.list.find(i=>i.value === this[item.type+'Req'])
     return `${item.name}：${data.name}`
    }
  },
  mounted(){
    this.init()
  }
}
</script>
<style lang="less">
.jobFairBox{
  .el-menu{
    .el-submenu:nth-child(1) .el-submenu__title{
      padding-left: 0;
    }
  }
  padding-top: 30px;
  overflow: hidden;
  .search-box{
    input{
      height: 40px;
      line-height: 40px;
      width: 700px;
    }
    button{
      height: 40px;
    }
  }
  hr{
    color:#10bbb8;
    background-color: #10bbb8;
  }
  .quicklySearch{
    margin: 10px 0 20px 0;
    ul{
      overflow: hidden;
      li{
        float: left;
        margin: 10px 20px 0px 0;
        cursor: pointer;
        &.active{
          color:#FC8863
        }
      }
    }
  }
  .sortSearch{
    margin: 20px 0;
    ul{
      overflow: hidden;
      li{
        float: left;
        margin-right: 40px;
        cursor: pointer;
      }
    }
  }
  .jobContainer{
    background-color: #f6f6f8;
    padding: 20px;
    text-align: left;
  }
  
    .loginModule{
      float:right;
      display: inline-block;
      width: 197px;
      border-radius: 5px;
      padding: 0 40px;
      background-color: #10bbb8;
      height: 297px;
      overflow: hidden;
      .title{
        font-size: 18px;
        text-align: center;
        color:#fff;
        padding: 20px 0;
      }
      .formItem{
        height: 36px;
        line-height: 36px;
        margin-top: 20px;
        padding: 0 10px;
        background-color: #fff;
        input{
          height: 26px;
          line-height: 26px;
          display: inline-block;
          width: 110px;
          outline: 0 none;
          border:0 none;
        }
        .psd{
          width: 140px;
        }
    }
    .toLogin{
      button{
        height: 36px;
        line-height: 36px;
        color: #10bbb8;
        outline: 0 none;
        border:0 none;
        font-size:18px;
        text-align: center;
        width: 100%;
        background-color: #fff;
        margin: 20px 0;
        &:hover{
          cursor: pointer;
        }
      }
    }
    .agree{
      input{
        vertical-align: -2px;
      }
      color: #fff;
      font-size: 12px;
    }
    }
}
</style>


